'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';

const CookieConsent = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [isRendered, setIsRendered] = useState(false);

  useEffect(() => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –ø—Ä–∏–Ω—è–ª –ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∫—É–∫–∏ —Ä–∞–Ω–µ–µ
    const cookieConsent = localStorage.getItem('cookieConsent');
    if (!cookieConsent) {
      // –ï—Å–ª–∏ –Ω–µ—Ç, —Å–Ω–∞—á–∞–ª–∞ —Ä–µ–Ω–¥–µ—Ä–∏–º –∫–æ–º–ø–æ–Ω–µ–Ω—Ç —Å–∫—Ä—ã—Ç—ã–º
      setIsRendered(true);
      // –ó–∞—Ç–µ–º —Å –Ω–µ–±–æ–ª—å—à–æ–π –∑–∞–¥–µ—Ä–∂–∫–æ–π –¥–µ–ª–∞–µ–º –µ–≥–æ –≤–∏–¥–∏–º—ã–º –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏
      const timer = setTimeout(() => {
        setIsVisible(true);
      }, 500);
      return () => clearTimeout(timer);
    }
  }, []);

  const acceptCookies = () => {
    // –°–Ω–∞—á–∞–ª–∞ –∑–∞–ø—É—Å–∫–∞–µ–º –∞–Ω–∏–º–∞—Ü–∏—é —Å–∫—Ä—ã—Ç–∏—è
    setIsVisible(false);
    // –ó–∞—Ç–µ–º –ø–æ—Å–ª–µ –∑–∞–≤–µ—Ä—à–µ–Ω–∏—è –∞–Ω–∏–º–∞—Ü–∏–∏ —É–¥–∞–ª—è–µ–º –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –∏–∑ DOM
    setTimeout(() => {
      setIsRendered(false);
      localStorage.setItem('cookieConsent', 'accepted');
    }, 300);
  };

  // –ï—Å–ª–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –Ω–µ –¥–æ–ª–∂–µ–Ω —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º null
  if (!isRendered) return null;

  return (
    <div 
      className={`fixed z-50 transition-all duration-300 ease-in-out
                  bottom-0 left-0 right-0 sm:bottom-4 sm:right-4 sm:left-auto
                  ${isVisible ? 'opacity-100 sm:scale-100 translate-y-0' : 'opacity-0 sm:scale-90 translate-y-4'}`}
    >
      <div className="w-full sm:max-w-xs">
        <div className="relative bg-white shadow-lg sm:border-0 border-t border-gray-100 sm:rounded-lg overflow-hidden">
          <div className="absolute top-0 left-0 w-full h-0.5 sm:h-1 gradient-bg"></div>
          
          {/* –ö–Ω–æ–ø–∫–∞ –∑–∞–∫—Ä—ã—Ç–∏—è (–∫—Ä–µ—Å—Ç–∏–∫) */}
          <button 
            onClick={acceptCookies}
            className="absolute top-2 right-2 w-5 h-5 flex items-center justify-center rounded-full hover:bg-gray-100 transition-colors"
            aria-label="–ó–∞–∫—Ä—ã—Ç—å —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ –∫—É–∫–∏"
          >
            <i className="fa-solid fa-xmark text-gray-400 text-xs"></i>
          </button>
          
          <div className="p-3">
            <div className="flex items-center gap-2 mb-2">
              <div className="w-6 h-6 rounded-full gradient-bg flex items-center justify-center text-white flex-shrink-0">
                <i className="fa-solid fa-cookie-bite text-xs"></i>
              </div>
              <h3 className="text-sm font-semibold">–ú—ã –∏—Å–ø–æ–ª—å–∑—É–µ–º –∫—É–∫–∏</h3>
            </div>
            
            <p className="text-xs text-gray-500">
              –§–∞–π–ª—ã cookie –∏—Å–ø–æ–ª—å–∑—É—é—Ç—Å—è –¥–ª—è —É–ª—É—á—à–µ–Ω–∏—è –≤–∞—à–µ–≥–æ –æ–ø—ã—Ç–∞ –∏ —Ä–∞–±–æ—Ç—ã –Ø–Ω–¥–µ–∫—Å.–ú–µ—Ç—Ä–∏–∫–∏. –ü—Ä–æ–¥–æ–ª–∂–∞—è, –≤—ã —Å–æ–≥–ª–∞—à–∞–µ—Ç–µ—Å—å —Å{' '}
              <Link href="/privacy" className="text-primary-blue hover:underline">
                –ø–æ–ª–∏—Ç–∏–∫–æ–π –∫–æ–Ω—Ñ–∏–¥–µ–Ω—Ü–∏–∞–ª—å–Ω–æ—Å—Ç–∏
              </Link>.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CookieConsent; 