'use client';

import { useState, useEffect, useRef } from 'react';
import Link from 'next/link';
import { ModalFormData, formatMessage, sendTelegramMessage } from '../services/TelegramService';

interface ModalProps {
  isOpen: boolean;
  onClose: () => void;
}

const Modal = ({ isOpen, onClose }: ModalProps) => {
  const [formData, setFormData] = useState<ModalFormData>({
    name: '',
    email: '',
    phone: ''
  });
  
  const [isFormSubmitting, setIsFormSubmitting] = useState(false);
  const [isFormSubmitted, setIsFormSubmitted] = useState(false);
  const [isFormVisible, setIsFormVisible] = useState(true);
  const [isSuccessVisible, setIsSuccessVisible] = useState(false);
  const [phoneError, setPhoneError] = useState<string | null>(null);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const modalRef = useRef<HTMLDivElement>(null);
  const phoneInputRef = useRef<HTMLInputElement>(null);
  
  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∏–∑–º–µ–Ω–µ–Ω–∏—è –ø–æ–ª–µ–π —Ñ–æ—Ä–º—ã
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };
  
  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø—Ä–∏–º–µ–Ω–µ–Ω–∏—è –º–∞—Å–∫–∏ –∫ —Ç–µ–ª–µ—Ñ–æ–Ω—É
  const applyPhoneMask = (value: string) => {
    // –£–¥–∞–ª—è–µ–º –≤—Å–µ –Ω–µ—Ü–∏—Ñ—Ä–æ–≤—ã–µ —Å–∏–º–≤–æ–ª—ã
    let digits = value.replace(/\D/g, '');
    
    // –ï—Å–ª–∏ –Ω–æ–º–µ—Ä –Ω–∞—á–∏–Ω–∞–µ—Ç—Å—è —Å 7 –∏–ª–∏ 8, —Å–æ—Ö—Ä–∞–Ω—è–µ–º –∫–∞–∫ –µ—Å—Ç—å, –∏–Ω–∞—á–µ –¥–æ–±–∞–≤–ª—è–µ–º 7 –≤ –Ω–∞—á–∞–ª–æ
    if (digits.length > 0 && (digits[0] !== '7' && digits[0] !== '8')) {
      digits = '7' + digits;
    } else if (digits.length === 0) {
      digits = '7';
    }
    
    // –ï—Å–ª–∏ –Ω–æ–º–µ—Ä –Ω–∞—á–∏–Ω–∞–µ—Ç—Å—è —Å 8, –∑–∞–º–µ–Ω—è–µ–º –Ω–∞ 7 (—Å—Ç–∞–Ω–¥–∞—Ä—Ç –†–§)
    if (digits[0] === '8') {
      digits = '7' + digits.substring(1);
    }
    
    // –û–±—Ä–µ–∑–∞–µ–º –¥–æ 11 —Ü–∏—Ñ—Ä, –µ—Å–ª–∏ –±–æ–ª—å—à–µ
    if (digits.length > 11) {
      digits = digits.substring(0, 11);
    }
    
    // –§–æ—Ä–º–∞—Ç–∏—Ä—É–µ–º –ø–æ —à–∞–±–ª–æ–Ω—É +7 (XXX) XXX-XX-XX
    let formatted = '';
    if (digits.length > 0) formatted += '+' + digits[0];
    if (digits.length > 1) formatted += ' (';
    if (digits.length > 1) formatted += digits.substring(1, Math.min(4, digits.length));
    if (digits.length > 4) formatted += ') ';
    if (digits.length > 4) formatted += digits.substring(4, Math.min(7, digits.length));
    if (digits.length > 7) formatted += '-';
    if (digits.length > 7) formatted += digits.substring(7, Math.min(9, digits.length));
    if (digits.length > 9) formatted += '-';
    if (digits.length > 9) formatted += digits.substring(9, 11);
    
    return formatted;
  };
  
  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –º–∞—Å–∫–∏ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
  const handlePhoneInput = (e: React.FormEvent<HTMLInputElement>) => {
    setPhoneError(null); // –°–±—Ä–∞—Å—ã–≤–∞–µ–º –æ—à–∏–±–∫—É –ø—Ä–∏ –≤–≤–æ–¥–µ
    const target = e.target as HTMLInputElement;
    target.value = applyPhoneMask(target.value);
    setFormData(prev => ({ ...prev, phone: target.value }));
  };
  
  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è change –¥–ª—è –∞–≤—Ç–æ–∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è
  const handlePhoneChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPhoneError(null); // –°–±—Ä–∞—Å—ã–≤–∞–µ–º –æ—à–∏–±–∫—É –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏
    const target = e.target as HTMLInputElement;
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –±—ã–ª –ª–∏ —ç—Ç–æ –≤–≤–æ–¥ –∞–≤—Ç–æ–∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è
    if (target.value && target.value !== formData.phone) {
      target.value = applyPhoneMask(target.value);
      setFormData(prev => ({ ...prev, phone: target.value }));
    }
  };
  
  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Ñ–æ–∫—É—Å–∞ –Ω–∞ –ø–æ–ª–µ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
  const handlePhoneFocus = (e: React.FocusEvent<HTMLInputElement>) => {
    if (e.target.value === '') {
      e.target.value = '+7 (';
      setFormData(prev => ({ ...prev, phone: e.target.value }));
    }
  };
  
  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –ø–æ—Ç–µ—Ä–∏ —Ñ–æ–∫—É—Å–∞ –Ω–∞ –ø–æ–ª–µ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
  const handlePhoneBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    if (e.target.value === '+7 (' || e.target.value === '+7') {
      e.target.value = '';
      setFormData(prev => ({ ...prev, phone: '' }));
    }
  };
  
  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∞–≤–∏—à –¥–ª—è –ø–æ–ª—è —Ç–µ–ª–µ—Ñ–æ–Ω–∞
  const handlePhoneKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    const target = e.target as HTMLInputElement;
    const cursorPosition = target.selectionStart || 0;
    
    // –ó–∞–ø—Ä–µ—â–∞–µ–º —É–¥–∞–ª–µ–Ω–∏–µ "+7" —Å –ø–æ–º–æ—â—å—é backspace –∏–ª–∏ delete
    if ((e.key === 'Backspace' && cursorPosition <= 3) || 
        (e.key === 'Delete' && cursorPosition < 2)) {
      e.preventDefault();
      return;
    }
    
    // –†–∞–∑—Ä–µ—à–∞–µ–º —Ç–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä—ã, —Å—Ç—Ä–µ–ª–∫–∏, backspace, delete –∏ Tab
    if (
      !/\d/.test(e.key) && 
      e.key !== 'Backspace' && 
      e.key !== 'Delete' && 
      e.key !== 'ArrowLeft' && 
      e.key !== 'ArrowRight' && 
      e.key !== 'Tab'
    ) {
      e.preventDefault();
    }
  };
  
  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // –ü—Ä–æ–≤–µ—Ä–∫–∞ –ø–æ–ª–Ω–æ—Ç—ã –≤–≤–æ–¥–∞ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
    const phonePattern = /^\+7 \(\d{3}\) \d{3}-\d{2}-\d{2}$/;
    if (!phonePattern.test(formData.phone)) {
      setPhoneError('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –ø–æ–ª–Ω–æ—Å—Ç—å—é');
      return;
    }
    
    setIsFormSubmitting(true);
    
    // –§–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –∏ –æ—Ç–ø—Ä–∞–≤–∫–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –≤ Telegram
    const message = formatMessage(formData, 'modal');
    const success = await sendTelegramMessage(message);
    
    setIsFormSubmitting(false);
    
    if (success) {
      // –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–π –ø–µ—Ä–µ—Ö–æ–¥ –º–µ–∂–¥—É —Ñ–æ—Ä–º–æ–π –∏ —Å–æ–æ–±—â–µ–Ω–∏–µ–º –æ–± —É—Å–ø–µ—Ö–µ
      setIsFormVisible(false);
      setTimeout(() => {
        setIsFormSubmitted(true);
        setIsSuccessVisible(true);
      }, 300);
      
      setFormData({ name: '', email: '', phone: '' });
    } else {
      // –£–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
      alert('–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.');
    }
  };
  
  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ —Å –∞–Ω–∏–º–∞—Ü–∏–µ–π
  const handleCloseModal = () => {
    setIsModalVisible(false);
    // –ñ–¥–µ–º –∑–∞–≤–µ—Ä—à–µ–Ω–∏—è –∞–Ω–∏–º–∞—Ü–∏–∏ –ø–µ—Ä–µ–¥ –≤—ã–ø–æ–ª–Ω–µ–Ω–∏–µ–º onClose
    setTimeout(() => {
      onClose();
    }, 300);
  };
  
  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –ø–æ –∫–ª–∏–∫—É –≤–Ω–µ –µ–≥–æ –æ–±–ª–∞—Å—Ç–∏
  const handleOutsideClick = (e: MouseEvent) => {
    if (modalRef.current && !modalRef.current.contains(e.target as Node)) {
      handleCloseModal();
    }
  };
  
  // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –Ω–∞ Escape
  const handleEscKey = (e: KeyboardEvent) => {
    if (e.key === 'Escape') {
      handleCloseModal();
    }
  };
  
  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –∫–æ–º–ø–µ–Ω—Å–∞—Ü–∏–∏ –∏—Å—á–µ–∑–Ω–æ–≤–µ–Ω–∏—è —Å–∫—Ä–æ–ª–ª-–±–∞—Ä–∞
  const lockBodyScroll = () => {
    // –ò–∑–º–µ—Ä—è–µ–º —à–∏—Ä–∏–Ω—É —Å–∫—Ä–æ–ª–ª-–±–∞—Ä–∞
    const scrollBarWidth = window.innerWidth - document.documentElement.clientWidth;
    // –°–æ—Ö—Ä–∞–Ω—è–µ–º —Ç–µ–∫—É—â–∏–π padding-right
    const currentPadding = parseInt(window.getComputedStyle(document.body).paddingRight, 10) || 0;
    // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º padding-right —Ä–∞–≤–Ω—ã–π —à–∏—Ä–∏–Ω–µ —Å–∫—Ä–æ–ª–ª-–±–∞—Ä–∞ + —Ç–µ–∫—É—â–∏–π padding
    document.body.style.paddingRight = `${scrollBarWidth + currentPadding}px`;
    document.body.style.overflow = 'hidden';
  };
  
  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –≤–æ–∑–≤—Ä–∞—â–µ–Ω–∏—è –∏—Å—Ö–æ–¥–Ω–æ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è
  const unlockBodyScroll = () => {
    document.body.style.paddingRight = '';
    document.body.style.overflow = '';
  };
  
  // –°–±—Ä–æ—Å —Å–æ—Å—Ç–æ—è–Ω–∏—è —Ñ–æ—Ä–º—ã –ø—Ä–∏ –∑–∞–∫—Ä—ã—Ç–∏–∏ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  const resetForm = () => {
    setFormData({ name: '', email: '', phone: '' });
    setIsFormSubmitted(false);
    setIsFormVisible(true);
    setIsSuccessVisible(false);
    setPhoneError(null);
  };
  
  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –∏ –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∞–≤—Ç–æ–∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è –ø—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  useEffect(() => {
    if (isOpen) {
      // –î–∞–µ–º –±—Ä–∞—É–∑–µ—Ä—É –≤—Ä–µ–º—è –Ω–∞ –∞–≤—Ç–æ–∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ
      const checkAutoFill = setTimeout(() => {
        const phoneInput = phoneInputRef.current;
        if (phoneInput && phoneInput.value && phoneInput.value !== formData.phone) {
          const formattedValue = applyPhoneMask(phoneInput.value);
          phoneInput.value = formattedValue;
          setFormData(prev => ({ ...prev, phone: formattedValue }));
        }
      }, 200);
      
      return () => clearTimeout(checkAutoFill);
    }
  }, [isOpen, formData.phone]);
  
  // –£—Å—Ç–∞–Ω–æ–≤–∫–∞ –≤–∏–¥–∏–º–æ—Å—Ç–∏ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –ø—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏/–∑–∞–∫—Ä—ã—Ç–∏–∏
  useEffect(() => {
    if (isOpen) {
      setIsModalVisible(true);
    }
  }, [isOpen]);
  
  // –≠—Ñ—Ñ–µ–∫—Ç—ã –¥–ª—è –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π
  useEffect(() => {
    if (isOpen) {
      lockBodyScroll(); // –ë–ª–æ–∫–∏—Ä–æ–≤–∫–∞ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ —Å –∫–æ–º–ø–µ–Ω—Å–∞—Ü–∏–µ–π
      document.addEventListener('mousedown', handleOutsideClick);
      document.addEventListener('keydown', handleEscKey);
    } else {
      unlockBodyScroll(); // –†–∞–∑–±–ª–æ–∫–∏—Ä–æ–≤–∫–∞ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏
      document.removeEventListener('mousedown', handleOutsideClick);
      document.removeEventListener('keydown', handleEscKey);
      // –ù–µ–±–æ–ª—å—à–∞—è –∑–∞–¥–µ—Ä–∂–∫–∞ –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ –∑–∞–∫—Ä—ã—Ç–∏—è
      setTimeout(resetForm, 300);
    }
    
    return () => {
      unlockBodyScroll();
      document.removeEventListener('mousedown', handleOutsideClick);
      document.removeEventListener('keydown', handleEscKey);
    };
  }, [isOpen]);
  
  // –ï—Å–ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∑–∞–∫—Ä—ã—Ç–æ –∏ –∞–Ω–∏–º–∞—Ü–∏—è –∑–∞–≤–µ—Ä—à–µ–Ω–∞, –Ω–µ —Ä–µ–Ω–¥–µ—Ä–∏–º –µ–≥–æ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ
  if (!isOpen && !isModalVisible) return null;
  
  return (
    <div 
      className={`fixed inset-0 z-50 flex items-center justify-center bg-primary-black/60 
        ${isModalVisible ? 'opacity-100' : 'opacity-0 pointer-events-none'} 
        transition-opacity duration-300 px-3 md:px-4 overflow-y-auto`}
    >
      <div 
        ref={modalRef}
        className={`bg-white w-full max-w-md rounded-2xl shadow-xl 
          transform ${isModalVisible ? 'translate-y-0 scale-100' : 'translate-y-8 scale-95'} 
          transition-all duration-300 overflow-hidden
          ${isModalVisible ? 'animate-[fadeIn_0.5s_forwards]' : 'animate-[fadeOut_0.3s_forwards]'} my-4 sm:my-5`}
      >
        {/* –°–æ–¥–µ—Ä–∂–∏–º–æ–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ */}
        <div id="modalContainer">
          {/* –§–æ—Ä–º–∞ –æ–±—Ä–∞—Ç–Ω–æ–π —Å–≤—è–∑–∏ */}
          <div 
            id="modalForm" 
            className={`transition-all duration-300 
              ${!isFormVisible ? 'opacity-0 transform -translate-y-4 pointer-events-none' : 'opacity-100'} 
              ${isFormSubmitted ? 'hidden' : ''}`}
          >
            {/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ */}
            <div className="relative px-5 sm:px-6 pt-5 sm:pt-6 pb-2 sm:pb-3">
              <button 
                onClick={handleCloseModal}
                className="absolute right-5 top-5 text-gray-400 hover:text-primary-black transition-colors"
              >
                <i className="fa-solid fa-xmark text-xl"></i>
              </button>
              <div className="text-center mb-4 sm:mb-5">
                <div className="w-14 h-14 sm:w-16 sm:h-16 gradient-bg rounded-xl flex items-center justify-center text-white mx-auto mb-4">
                  <i className="fa-solid fa-rocket text-xl sm:text-2xl"></i>
                </div>
                <h3 className="font-bounded text-xl font-bold mb-2">–û–±—Ä–∞—Ç–Ω—ã–π –∑–≤–æ–Ω–æ–∫</h3>
                <p className="text-gray-500 text-sm sm:text-base max-w-sm mx-auto">–ó–∞–ø–æ–ª–Ω–∏—Ç–µ —Ñ–æ—Ä–º—É, –∏ –º—ã —Å–≤—è–∂–µ–º—Å—è —Å –≤–∞–º–∏ –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è</p>
              </div>
            </div>
            
            {/* –¢–µ–ª–æ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ */}
            <div className="px-5 sm:px-6 pb-5 sm:pb-6">
              <form className="space-y-4" onSubmit={handleSubmit}>
                <div>
                  <label htmlFor="modal-name" className="block mb-1.5 font-medium text-gray-700">–ò–º—è</label>
                  <input 
                    type="text" 
                    id="modal-name" 
                    name="name" 
                    value={formData.name}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-2.5 bg-primary-gray border-0 rounded-xl focus:outline-none focus:ring-2 focus:ring-primary-blue transition-all placeholder-gray-400 hover:shadow-sm"
                    placeholder="–ò–≤–∞–Ω –ò–≤–∞–Ω–æ–≤"
                  />
                </div>
                
                <div>
                  <label htmlFor="modal-email" className="block mb-1.5 font-medium text-gray-700">Email</label>
                  <input 
                    type="email" 
                    id="modal-email" 
                    name="email" 
                    value={formData.email}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-2.5 bg-primary-gray border-0 rounded-xl focus:outline-none focus:ring-2 focus:ring-primary-blue transition-all placeholder-gray-400 hover:shadow-sm"
                    placeholder="example@mail.ru"
                  />
                </div>
                
                <div>
                  <label htmlFor="modal-phone" className="block mb-1.5 font-medium text-gray-700">–¢–µ–ª–µ—Ñ–æ–Ω</label>
                  <input 
                    type="tel" 
                    id="modal-phone" 
                    name="phone" 
                    value={formData.phone}
                    onInput={handlePhoneInput}
                    onChange={handlePhoneChange}
                    onFocus={handlePhoneFocus}
                    onBlur={handlePhoneBlur}
                    onKeyDown={handlePhoneKeyDown}
                    ref={phoneInputRef}
                    required
                    className={`w-full px-4 py-2.5 bg-primary-gray border-0 rounded-xl 
                      focus:outline-none focus:ring-2 
                      ${phoneError ? 'focus:ring-primary-red ring-2 ring-primary-red' : 'focus:ring-primary-blue'} 
                      transition-all placeholder-gray-400 hover:shadow-sm`}
                    placeholder="+7 (___) ___-__-__"
                    autoComplete="tel"
                  />
                  {phoneError && (
                    <div className="mt-1.5 text-primary-red text-sm flex items-center">
                      <i className="fa-solid fa-circle-exclamation mr-1"></i>
                      <span>{phoneError}</span>
                    </div>
                  )}
                </div>
                
                <div className="pt-2 sm:pt-3">
                  <button 
                    type="submit" 
                    disabled={isFormSubmitting}
                    className="gradient-bg text-white font-semibold py-2.5 px-6 rounded-xl hover:shadow-button transform hover:-translate-y-1 transition-all w-full btn-animated text-base disabled:opacity-70 disabled:cursor-not-allowed"
                  >
                    {isFormSubmitting ? (
                      <><i className="fa-solid fa-circle-notch fa-spin mr-2"></i>–û—Ç–ø—Ä–∞–≤–∫–∞...</>
                    ) : '–û—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞—è–≤–∫—É'}
                  </button>
                </div>
                
                <div className="text-sm text-gray-500 text-center mt-3">
                  –ù–∞–∂–∏–º–∞—è –Ω–∞ –∫–Ω–æ–ø–∫—É, –≤—ã –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–∞–µ—Ç–µ, —á—Ç–æ –æ–∑–Ω–∞–∫–æ–º–ª–µ–Ω—ã –∏ —Å–æ–≥–ª–∞—Å–Ω—ã —Å <Link href="/privacy" className="text-primary-blue hover:underline">–ø–æ–ª–∏—Ç–∏–∫–æ–π –∫–æ–Ω—Ñ–∏–¥–µ–Ω—Ü–∏–∞–ª—å–Ω–æ—Å—Ç–∏</Link>
                </div>
              </form>
            </div>
          </div>
          
          {/* –°–æ–æ–±—â–µ–Ω–∏–µ –æ–± —É—Å–ø–µ—à–Ω–æ–π –æ—Ç–ø—Ä–∞–≤–∫–µ */}
          <div 
            id="modalSuccess" 
            className={`p-5 sm:p-6 text-center transition-all duration-500 
              ${isSuccessVisible ? 'opacity-100 transform scale-100' : 'opacity-0 transform scale-95'} 
              ${!isFormSubmitted ? 'hidden' : ''}`}
          >
            <div className="relative mx-auto mb-5">
              <div className="w-18 h-18 gradient-bg rounded-full flex items-center justify-center text-white mx-auto animate-[fadeIn_0.6s_forwards]">
                <i className="fa-solid fa-check text-2xl sm:text-3xl"></i>
              </div>
              <div className="absolute -bottom-1 -left-1 w-22 h-22 rounded-full bg-primary-blue/5 -z-10 blur-xl"></div>
            </div>
            
            <h3 className="font-bounded text-xl font-bold mb-2.5 animate-[fadeIn_0.7s_forwards]">–ó–∞—è–≤–∫–∞ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞!</h3>
            <p className="text-gray-500 text-base mb-4 animate-[fadeIn_0.8s_forwards]">–ú—ã –ø–æ–ª—É—á–∏–ª–∏ –≤–∞—à—É –∑–∞—è–≤–∫—É –∏ —Å–≤—è–∂–µ–º—Å—è —Å –≤–∞–º–∏ –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è.</p>
            
            <div className="flex justify-center items-center gap-2 text-xs sm:text-sm text-gray-400 mb-4 animate-[fadeIn_0.9s_forwards]">
              <div className="w-2 h-2 rounded-full bg-primary-blue/20"></div>
              <span>–°–ø–µ—Ü–∏–∞–ª–∏—Å—Ç —Å–≤—è–∂–µ—Ç—Å—è —Å –≤–∞–º–∏ –≤ —Ç–µ—á–µ–Ω–∏–µ 24 —á–∞—Å–æ–≤</span>
              <div className="w-2 h-2 rounded-full bg-primary-blue/20"></div>
            </div>
            
            <button 
              type="button" 
              onClick={handleCloseModal}
              className="gradient-bg text-white font-medium py-2.5 px-6 rounded-xl hover:shadow-button transform hover:-translate-y-1 transition-all text-center w-full btn-animated mt-3 text-base animate-[fadeIn_1s_forwards]"
            >
              –û—Ç–ª–∏—á–Ω–æ
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Modal; 